<template>
  <el-card>
    <el-button @click="info.isShow=true" type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
    <el-divider />
    <el-table
      :data="menuList"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
    >
      <el-table-column prop="id" label="Id" min-width="80"></el-table-column>
      <el-table-column prop="title" label="菜单名称" min-width="180"></el-table-column>
      <el-table-column prop="icon" label="图标" min-width="180">
        <!-- 
          <slot row="" msg=""></slot>
        -->
        <template v-slot="$props">
          <i :class="$props.row.icon"></i>
        </template>
      </el-table-column>
      <el-table-column prop="url" label="地址" min-width="180"></el-table-column>
      <el-table-column prop="status" label="状态" min-width="180">
        <template slot-scope="$props">
          <el-tag type="success" v-if="$props.row.status===1">正常</el-tag>
          <el-tag type="danger" v-else>禁用</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
        <!--   -->
        <template slot-scope="$props">
          <el-button
            @click="edit($props.row)"
            type="success"
            circle
            size="mini"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            @click="del($props.row.id)"
            type="danger"
            circle
            size="mini"
            icon="el-icon-delete"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 对话框 -->
    <!-- 父组件向子组件传递数据 -->
    <Dialog ref="dialog" :info="info" @updateList="updateList" />
  </el-card>
</template>

<script>
import Dialog from "./Dialog"
import { mapState, mapActions,mapMutations } from "vuex"
export default {
  components: {
    Dialog
  },
  data() {
    return {
      // 即将传递给子组件Dialog的数据
      info: {
        isShow: false
      }
    }
  },
  computed: {
    ...mapState({ menuList: state => state.menu.menuList })
  },
  methods: {
    ...mapActions('menu', ['getMenuListAction']),
    ...mapMutations('menu',['menuListInit']),
    // 自定义事件updateList处理函数
    updateList() {
      // 重新请求菜单列表
      this.getMenuListAction()
    },
    // 编辑菜单
    edit(row) {
      // 通过ref调用子组件Dialog的setFormData
      // 1-将待编辑的菜单信息在表单中显示
      this.$refs.dialog.setFormData(row)
      // 2-显示表单所在的组件
      this.info.isShow = true
    },
    // 删除菜单
    del(id) {
      this.$myConfirm(()=>{
        this.$http.post('/menudelete',{id}).then(res=>{
          if(res.code===200){
            // 更新菜单列表
            this.$success(res.msg)
            // 方案1: 调用异步action重新请求数据接口
            // this.getMenuListAction()
            // 方案2: 调用同步mutation更新数据
            this.menuListInit(res.list)
          }
        })
      })
    }
  },
  created() {
    this.getMenuListAction()
  }
};
</script>

<style>
</style>


