<template>
  <el-dialog @close="resetInfo" :title="formData.id>0?'编辑':'新增'" :visible.sync="info.isShow">
    <el-form ref="addForm" label-width="100px" :model="formData" :rules="rules">
      <el-form-item label="类型" prop="type">
        <el-radio-group v-model="formData.type">
          <el-radio :label="1">目录</el-radio>
          <el-radio :label="2">菜单</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="上级" v-if="formData.type===2" prop="pid">
        <el-select v-model="formData.pid" placeholder="请选择">
          <el-option label="顶级菜单" :value="0"></el-option>
          <el-option
            v-for="item in topMenuList"
            :key="item.id"
            :label="item.title"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名称" prop="title">
        <el-input v-model="formData.title" placeholder="请输入名称"></el-input>
      </el-form-item>
      <el-form-item label="图标" v-if="formData.type===1" prop="icon">
        <el-input v-model="formData.icon" placeholder="请输入图标"></el-input>
      </el-form-item>
      <el-form-item label="地址" v-if="formData.type===2" prop="url">
        <el-input v-model="formData.url" placeholder="请输入地址"></el-input>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-radio-group v-model="formData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="info.isShow = false">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapState } from "vuex"
export default {
  // 接收来自父组件的数据
  props: ["info"],
  computed: {
    ...mapState({ topMenuList: state => state.menu.menuList })
  },
  data() {
    return {
      // 和表单进行绑定的数据对象
      formData: {
        id: 0,
        // 上级目录的id
        pid: 0,
        title: "",
        icon: "",
        type: 1,
        url: "",
        status: 1
      },
      // 表单校验规则
      rules: {
        title: { required: true, message: "请输入菜单标题", trigger: "blur" },
        type: { required: true, message: "请输入选择类型", trigger: "blur" },
        status: { required: true, message: "请输选择状态", trigger: "blur" }
      }
    }
  },
  methods: {
    //   表单提交
    submitForm() {
      this.$refs.addForm.validate(valid => {
        // valid: 系统自动注入的参数, true表示表单校验通过, false未通过
        if (valid) {
          const url = this.formData.id > 0 ? "/menuedit" : "/menuadd"
          this.$http.post(url, this.formData).then(res => {
            // 1-更新列表:
            // (1)-将更新菜单列表的异步action方法在此处重新调用一次
            // (2)-通过自定义事件, 通知父组件重新请求数据列表
            if (res.code === 200) {
              // 成功提示
              this.$message({ type: "success", message: res.msg })
              // 触发自定义事件
              this.$emit("updateList")
              // 重置信息
              this.resetInfo()
            } else {
              this.$message({ type: "error", message: res.msg })
            }
          })
        } else {
          console.log("表单校验未通过")
        }
      });
    },
    // 重置信息
    resetInfo() {
      // 关闭模态框
      this.info.isShow = false
      // 重置表单
      this.formData = {
        id:0,
        pid: 0,
        title: "",
        icon: "",
        type: 1,
        url: "",
        status: 1
      }
    },
    // 设置表单信息
    setFormData(obj) {
      this.formData = { ...obj }
    }
  }
};
</script>

<style>

</style>


